/* üìå Main Page Container */
.heritage-product-image {
  width: 100%;
}

.heritage-product-detail {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 50px 50px;
  max-width: 1500px;
  margin: 0 auto;
  box-sizing: border-box;
  background: #121212;
  color: white;
}

.order-to-build-disclaimer {
  font-size: 12px;
}

/* üîÑ Main Content Layout - Adjusted for Proper Balance */
.heritage-product-content {
  /* display: flex; */
  justify-content: space-between;
  align-items: flex-start;
  width: 100%;
  gap: 60px;
  max-width: 1200px;
  box-sizing: border-box;
}

/* üìå Left Side: Product Image + Sound Charts */
.heritage-product-visuals {
  flex: 1;
  max-width: 58%;
  display: flex;
  flex-direction: column;
  align-items: center;
}

/* Product Image */
.heritage-product-image img {
  width: 100%;
  height: auto;
  border-radius: 4px;
  box-shadow: 0 6px 15px rgba(0, 0, 0, 0.3);
  transition: transform 0.3s ease;
}

.heritage-product-image img:hover {
  transform: scale(1.02);
}

/* üìå Sound Profile Charts */
.heritage-sound-charts {
  width: 100%;
  display: flex;
  flex-direction: column;
  gap: 20px;
  margin-top: 25px;
}

/* üìå Right Side: Features + Customization */
.heritage-product-details {
  flex: 1;
  max-width: 42%;
  display: flex;
  flex-direction: column;
  gap: 25px;
}

/* üìå Customization Panel */
.heritage-product-options {
  background: rgba(255, 255, 255, 0.08);
  padding: 22px;
  border-radius: 10px;
  border: 1px solid rgba(255, 255, 255, 0.15);
  width: 100%;
  color: white;
  box-sizing: border-box;
  backdrop-filter: blur(8px);
  margin-top:20px
}

/* üìå Section Titles */
.heritage-product-options h2 {
  font-size: 20px;
  margin-bottom: 14px;
  color: #e0e0e0;
}

/* üìå Labels for Dropdowns */
.heritage-product-options label {
  display: block;
  font-size: 15px;
  font-weight: 600;
  margin-bottom: 6px;
  color: #ccc;
}

/* üìå Dropdown Styling */
.heritage-product-options select {
  width: 100%;
  padding: 10px;
  font-size: 15px;
  border-radius: 6px;
  border: 1px solid #666;
  background: #1a1a1a;
  color: white;
  cursor: pointer;
  margin-bottom: 12px;
}

/* üìå Total Price Styling */
.heritage-product-options .total-price {
  font-size: 22px;
  font-weight: bold;
  margin-top: 14px;
  color: #f0f0f0;
  text-align: center;
}

.heritage-detail-price {
  font-size: 25px;
  font-weight: 600;
  color: #ffde08;
  text-align: center;
}

.add-to-cart-button {
  background-color: #4135ea;
  color: rgb(255, 255, 255);
  font-size: 18px;
  font-weight: bold;
  padding: 12px 20px;
  /* border-radius: 8px; */
  width: 100%;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.add-to-cart-button:hover {
  background-color: rgb(43, 82, 255);
}

.notify-me-button {
  background-color: #888;
  color: white;
  font-size: 18px;
  font-weight: bold;
  padding: 12px 20px;
  border-radius: 8px;
  cursor: not-allowed;
  opacity: 0.7;
}

.loading-button {
  background-color: #ddd;
  color: black;
  font-size: 16px;
  font-weight: bold;
  padding: 10px;
  border-radius: 6px;
  cursor: not-allowed;
  opacity: 0.5;
}

/* Grey out locked dropdowns */
.disabled-dropdown {
  background-color: #ddd !important;
  color: #666 !important;
  cursor: not-allowed;
}

/* Change Selection Button Flicker */
@keyframes flicker {
  0% { background-color: #ffcc00; }
  50% { background-color: #ffaa00; }
  100% { background-color: #ffcc00; }
}

/* üìå Footer Adjustments */
.footer {
  margin-top: 40px;
  padding: 20px;
  background: #181818;
  text-align: center;
  color: white;
}

/* üìå Responsive Adjustments */
@media (max-width: 1024px) {
  .heritage-product-detail {
      padding: 21px;
  }

  .heritage-product-content {
      flex-direction: column;
      align-items: center;
      gap: 35px;
  }

  .heritage-product-visuals {
      max-width: 100%;
  }

  .heritage-product-details {
      max-width: 100%;
  }

  .heritage-sound-charts {
      gap: 15px;
  }
}

@media (max-width: 768px) {
  .heritage-product-detail h1 {
      font-size: 28px;
  }

  .heritage-product-options {
      padding: 10px;
  }

  .heritage-product-options h2 {
      font-size: 20px;
  }

  .heritage-product-options select {
      font-size: 14px;
      padding: 8px;
  }

  .heritage-product-options button {
      font-size: 15px;
      padding: 12px;
  }
}